.topbarContainer {
    width: 100%;
    height: 50px;
    position: sticky;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color:white;
    background-color:rgb(0, 176, 235);
}

.topbarLeft {
    font-size: 30px;
    color:white;
    margin-left: 50px;
}

.topbarRight {
    display: flex;
    flex-direction: column;
    margin-right: 50px;
}